}

.section-aulas .content-frontend .images-right img:nth-child(1),
.section-aulas .content-extra .images-right img:nth-child(1) {
  align-self: flex-start;
}

.section-aulas .content-frontend .images-right img:nth-child(2),
.section-aulas .content-extra .images-right img:nth-child(2) {
  align-self: flex-end;

  -webkit-animation-delay: 1s;

