}

.section-aulas .content-frontend .images-right img:nth-child(3),
.section-aulas .content-extra .images-right img:nth-child(3) {
  align-self: flex-start;

  -webkit-animation-delay: 2s;

          animation-delay: 2s;
}

.section-aulas .content-backend .images-left img:nth-child(1) {
  align-self: flex-end;
}

.section-aulas .content-backend .images-left img:nth-child(2) {
